<template>
  <div id="snake-segment" :class="isHead ? 'head' : 'food'" :style="{ gridRowStart: y, gridColumnStart: x }"></div>
</template>

<script>
  export default {
    props: {
      x: {
        type: Number,
        requiered: true,
      },
      y: {
        type: Number,
        requiered: true,
      },
      isHead: {
        type: Boolean,
        default: false,
      },
    },
  }
</script>

<style>
  .head {
    background-image: url('../assets/snake.webp');
    background-size: 100%;
    background-repeat: no-repeat;
  }

  .food {
    background-image: url('../assets/food.webp');
    background-size: 100%;
    background-repeat: no-repeat;
  }
</style>
